import { IProvider } from "react-simple-auth";

export interface Session {
    accessToken: string;
    tokenType: string;
    expireDurationSeconds: number;
    state: string;
    scope: string;
}

export const enum Scopes {
    "gmail.readonly" = "https://www.googleapis.com/auth/gmail.readonly",
}

export const buildGoogleProvider = (
    clientId: string,
    scopes: Scopes[] = [Scopes["gmail.readonly"]],
): IProvider<Session> => ({
    buildAuthorizeUrl() {
        return (
            "https://accounts.google.com/o/oauth2/v2/auth" +
            `?scope=${encodeScopes(scopes)}` +
            "&include_granted_scopes=true" +
            "&state=state_parameter_passthrough_value" +
            `&redirect_uri=${encodeURIComponent(`${window.location.origin}/redirect.html`)}` +
            "&response_type=token" +
            `&client_id=${clientId}`
        );
    },

    extractError(redirectUrl: string) {
        const errorReason: string = getParameters(redirectUrl, "error");

        return errorReason ? new Error(errorReason) : undefined;
    },

    extractSession(redirectUrl: string): Session {
        const accessToken: string = getParameters(redirectUrl, "access_token");
        const tokenType: string = getParameters(redirectUrl, "token_type");
        const state: string = getParameters(redirectUrl, "state");
        const scope: string = getParameters(redirectUrl, "scope");
        const expireDurationSeconds = parseInt(getParameters(redirectUrl, "expires_in"), 10);

        return {
            accessToken,
            expireDurationSeconds,
            tokenType,
            state,
            scope,
        };
    },

    validateSession(_session: Session): boolean {
        // TODO: Add validation
        return true;
    },

    getAccessToken(session: Session, _resourceId: string): string {
        return session.accessToken;
    },

    getSignOutUrl(token: string): string {
        return `https://accounts.google.com/o/oauth2/revoke?token=${encodeURIComponent(token)}`;
    },
});

const getParameters = (redirectUrl: string, name: string) => {
    const regex = new RegExp(`${name}=([^&]+)`);

    let value: string = null;
    const valueMatch = redirectUrl.match(regex);
    if (valueMatch) {
        value = valueMatch[1];
    }

    return value;
};

const encodeScopes = (scopes: Scopes[]) => {
    return encodeURIComponent(scopes.join(","));
};
