import { connect } from "react-redux";

import { RootState } from "reducers";

import Navigation, { NavigationData, NavigationHandlers } from "./Navigation";

const mapStateToProps = (state: RootState): NavigationData => {
    const path = state.router.location.pathname;

    return {
        path,
    };
};

const mapDispatchToProps: NavigationHandlers = {};

const NavigationModule = connect(
    mapStateToProps,
    mapDispatchToProps,
)(Navigation);

export { NavigationModule as default, NavigationModule as Navigation };
