import * as React from "react";

import Tab, { TabProps } from "@material-ui/core/Tab";

import { Link } from "react-router-dom";

export interface TabLinkProps extends TabProps {
    to: string;
}

// Hack: https://github.com/mui-org/material-ui/issues/9106

export class TabLink extends React.PureComponent<TabLinkProps> {
    public render() {
        const { to, ...tabProps } = this.props;
        // tslint:disable-next-line:no-any
        return <Tab {...tabProps} {...{ component: Link, to } as any} />;
    }
}
