import * as React from "react";

import { ButtonBaseProps } from "@material-ui/core/ButtonBase";
import Button from "@material-ui/core/Button";

export interface UploadButtonProps extends ButtonBaseProps {
    component?: React.ReactType<ButtonBaseProps>;

    accept?: string;
    maxLength?: number;

    onUpload: (event: React.ChangeEvent<HTMLInputElement>) => void;
}

export class UploadButton extends React.PureComponent<UploadButtonProps> {
    public render() {
        const {
            accept,
            maxLength,
            children,
            component = Button,
            onUpload,
            ...buttonProps
        } = this.props;

        const Wrapper = component;

        return (
            <Wrapper {...buttonProps} component="label">
                <input
                    type="file"
                    style={{ display: "none" }}
                    onChange={onUpload}
                    {...{ accept, maxLength }}
                />
                {children}
            </Wrapper>
        );
    }
}

