import * as React from "react";
import { NamespacesConsumer } from "react-i18next";

export interface LocalizeData {
    id: string;
}

export interface LocalizeHandlers {
}

class Localize extends React.PureComponent<LocalizeData & LocalizeHandlers> {
    public render() {
        const { id } = this.props;
        return (
            <NamespacesConsumer>
                {t => {
                    return t(id);
                }}
            </NamespacesConsumer>
        );
    }
}

export { Localize, Localize as default };
