import * as React from "react";

import { UploadButton } from "modules/button";
import { VideoPlayer } from "./video-player";
import { IconButton, TextField } from "@material-ui/core";

import AttachmentIcon from "@material-ui/icons/Attachment";
import { Localize } from "modules/localize";

import styles from "./VideoReactExample.scss";

export interface VideoReactExampleData {}

export interface VideoReactExampleHandlers {}

interface FileInfo {
    name: string;
    url: string;
}

export interface VideoReactExampleState {
    videoFile?: FileInfo;
    subtitlesFile?: FileInfo;
}

export class VideoReactExample extends React.PureComponent<
    VideoReactExampleData & VideoReactExampleHandlers,
    VideoReactExampleState
> {
    public state: VideoReactExampleState = {};

    public render() {
        const { videoFile, subtitlesFile } = this.state;

        return (
            <div className={styles["video-player"]}>
                {this.renderControlPanel()}
                <VideoPlayer
                    className={styles["video-player__player"]}
                    videoFileURL={videoFile && videoFile.url}
                    subtitlesFileURL={subtitlesFile && subtitlesFile.url}
                />
            </div>
        );
    }

    private renderControlPanel = () => {
        const { subtitlesFile, videoFile } = this.state;
        return (
            <div className={styles["video-player__control-panel"]}>
                <TextField
                    disabled
                    className={styles["video-player__control-item"]}
                    value={videoFile ? videoFile.name : ""}
                    helperText={<Localize id="videoPlayer:video" />}
                    InputProps={{
                        startAdornment: (
                            <UploadButton
                                accept=".mp4,.mkv"
                                maxLength={1}
                                onUpload={this.handleVideoUpload}
                                component={IconButton}
                            >
                                <AttachmentIcon />
                            </UploadButton>
                        ),
                    }}
                />
                <TextField
                    disabled
                    className={styles["video-player__control-item"]}
                    value={subtitlesFile ? subtitlesFile.name : ""}
                    helperText={<Localize id="videoPlayer:subtitle" />}
                    InputProps={{
                        startAdornment: (
                            <UploadButton
                                accept=".vtt"
                                maxLength={1}
                                onUpload={this.handleSubtitlesUpload}
                                component={IconButton}
                            >
                                <AttachmentIcon />
                            </UploadButton>
                        ),
                    }}
                />
            </div>
        );
    };

    private handleVideoUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
        const targetEvent = event.currentTarget;

        const files = targetEvent.files;

        if (files.length === 1) {
            const file = files[0];

            this.setState({
                videoFile: {
                    name: file.name,
                    url: URL.createObjectURL(file),
                },
            });
        }
    };

    private handleSubtitlesUpload = (event: React.ChangeEvent<HTMLInputElement>) => {
        const targetEvent = event.currentTarget;

        const files = targetEvent.files;

        if (files.length === 1) {
            const file = files[0];
            this.setState({
                subtitlesFile: {
                    name: file.name,
                    url: URL.createObjectURL(file),
                },
            });
        }
    };
}
