import * as React from "react";

import { Redirect } from "react-router";

import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";

import { Localize } from "modules/localize";

export interface LoginData {
    isAuthenticated: boolean;
    redirectPath?: string;
}

export interface LoginHandlers {
    signUp: () => void;
}

class Login extends React.PureComponent<LoginData & LoginHandlers> {
    public render() {
        const { isAuthenticated, redirectPath } = this.props;

        if (isAuthenticated) {
            return <Redirect to={redirectPath} />;
        }

        return (
            <Grid container justify="center" alignItems="center" direction="column">
                <Grid item>
                    <Typography variant="h6" color="inherit">
                        <Localize id="login:needAuthorize" />
                    </Typography>
                </Grid>
                <Grid item>
                    <Button variant="contained" color="primary" onClick={this.props.signUp}>
                        <Localize id="login:signUp" />
                    </Button>
                </Grid>
            </Grid>
        );
    }
}

export default Login;
