import { connect } from "react-redux";

import { RootState } from "reducers";
import { signUp } from "reducers/actions";

import Login, { LoginData, LoginHandlers } from "./Login";

const getRedirectPath = (path: string) => {
    const result = /.*redirectPath=(?<path>.*).*/.exec(path);
    return (result && result[1]) || "/";
};

const mapStateToProps = (state: RootState): LoginData => {
    const redirectPath = getRedirectPath(state.router.location.search);

    return {
        isAuthenticated: Boolean(state.auth.tokenInfo),
        redirectPath,
    };
};

const mapDispatchToProps: LoginHandlers = {
    signUp,
};

const LoginModule = connect(
    mapStateToProps,
    mapDispatchToProps,
)(Login);

export { LoginModule as default, LoginModule as Login };
