import { actionCreator } from "./action-helpers";

import { Message } from "model/message";

export const loadMessages = actionCreator("messages/LOAD");

export const loadMessagesSuccess = actionCreator(
    "messages/LOAD_SUCCESS",
    (messages: Message[]) => ({
        messages,
    }),
);

export const loadMessagesFailure = actionCreator(
    "messages/LOAD_FAILURE",
    (erorr: Error, message: string) => ({ erorr, message }),
);
