import { actionCreator } from "./action-helpers";

import { TokenInfo } from "model";

export const signUp = actionCreator("signUp/SIGN_UP_REQUEST");

export const signUpSuccess = actionCreator("signUp/SIGN_UP_SUCCESS", (tokenInfo: TokenInfo) => ({
    tokenInfo,
}));

export const signUpFail = actionCreator(
    "signUp/SIGN_UP_FAILURE",
    (erorr: Error, message: string) => ({ erorr, message }),
);
