import { bindActionCreators } from "redux";
import { connectAdvanced } from "react-redux";

import { RootState } from "reducers";

import ProtectedRoute from "./ProtectedRoute";

// TODO: Add Component types inference
import { RouteProps } from "react-router";
import { ProtectedRouteData, ProtectedRouteHandlers } from "./ProtectedRoute";

const mapStateToProps = (state: RootState): ProtectedRouteData => {
    // TODO: Add token validation
    return {
        isAuthenticated: Boolean(state.auth.tokenInfo),
        currentPath: state.router.location.pathname,
    };
};

const mapDispatchToProps = (dispatch): ProtectedRouteHandlers => {
    const actionCreators = {};
    return bindActionCreators(actionCreators, dispatch);
};

function selectorFactory(dispatch) {
    const actions = mapDispatchToProps(dispatch);

    return (
        nextState: RootState,
        newOwnProps: RouteProps,
    ): ProtectedRouteData & ProtectedRouteHandlers => ({
        ...mapStateToProps(nextState),
        ...actions,
        ...newOwnProps,
    });
}

const ProtectedRouteModule = connectAdvanced(selectorFactory)(ProtectedRoute);

export { ProtectedRouteModule as default, ProtectedRouteModule as ProtectedRoute };
