import { Action as ReduxAction } from "redux";

export interface Action<T extends string = string> extends ReduxAction {
    readonly type: T;
}

export type ActionWithPayload<T extends string, Payload> = Action<T> & Payload;

interface ActionCreator<T extends string = string> {
    (): Action<T>;
    type: T;
}

interface ActionWithPayloadCreator<
    T extends string = string,
    ArgsType extends unknown[] = [],
    PayloadType = {}
> {
    (...args: ArgsType): ActionWithPayload<T, PayloadType>;
    type: T;
}

export function actionCreator<T extends string>(type: T): ActionCreator<T>;
export function actionCreator<T extends string, ArgsType extends unknown[], PayloadType>(
    type: T,
    mapper?: (...args: ArgsType) => PayloadType,
): ActionWithPayloadCreator<T, ArgsType, PayloadType>;

export function actionCreator(type, mapper?) {
    // tslint:disable-next-line:no-any
    const actionCreator: any = function(...args) {
        const payload = mapper ? mapper(...args) : undefined;
        return createAction(type, payload);
    };

    actionCreator.type = type;

    return actionCreator;
}

export function createAction<T extends string, P>(type: T): Action<T>;
export function createAction<T extends string, P>(type: T, payload: P): ActionWithPayload<T, P>;

export function createAction(type, payload?) {
    return payload === undefined ? { type } : { type, ...payload };
}
