export type Handler<T, P> = (result: T) => P | Promise<P>;

export const Headers = {
    AcceptJson: { Accept: "application/json" },
    Authorization: (token: string) => ({ Authorization: token }),
    JsonContent: { "Content-Type": "application/json" },
};

export const jsonHandler = <T>(response: Response): Promise<T> =>
    response.json().catch(() => new Error());

export class ApiRequest<T = Response> {
    public static get(uri: string) {
        return new ApiRequest(uri, { method: "GET" });
    }

    public static post(uri: string) {
        return new ApiRequest(uri, { method: "POST" });
    }

    private request: Request;
    private handlers: Handler<unknown, unknown>[];

    private constructor(
        input: RequestInfo,
        init: RequestInit = {},
        handlers: Handler<unknown, unknown>[] = [],
    ) {
        this.request = new Request(input, init);
        this.handlers = handlers;
    }

    public withHeaders(...extraHeaders: HeadersInit[]) {
        const headers: HeadersInit = extraHeaders.reduce(
            (headers, extra) => Object.assign(headers, extra),
            { ...(this.request.headers || {}) },
        );
        return new ApiRequest(this.request, { headers }, this.handlers);
    }

    public handler = <P>(handler: (result: T) => P | Promise<P>): ApiRequest<P> => {
        return new ApiRequest(this.request, {}, [...this.handlers, handler]);
    };

    public execute = (): Promise<T> => {
        return this.handlers.reduce(
            (promise: Promise<unknown>, handler) => promise.then(handler),
            fetch(this.request),
        ) as Promise<T>;
    };
}
