import { call, put, select, takeLatest } from "redux-saga/effects";

import RSA from "react-simple-auth";
import { requestProfile, signUp, signUpFail, signUpSuccess } from "reducers/actions";

import { Session, buildGoogleProvider } from "./googleProvider";
import { RootState } from "reducers";

export function* signUpSaga() {
    yield takeLatest(signUp.type, processSignUp);
}

function* processSignUp(_action: ReturnType<typeof signUp>) {
    try {
        const clientId = yield call(getClientId);
        const googleProvider = buildGoogleProvider(clientId);

        const session: Session = yield call(RSA.acquireTokenAsync, googleProvider);

        yield put(signUpSuccess({ token: session.accessToken }));

        // TODO: Не должно тут быть
        yield put(requestProfile());
    } catch (err) {
        yield put(signUpFail(err, err.toString()));
    }
}

export function* getClientId() {
    const clientId = yield select((state: RootState) => state.settings.clientId);
    return clientId;
}
