import { ApiRequest, jsonHandler } from "api/apiRequest";

const baseUri = "https://www.googleapis.com/gmail/v1";

export interface Message {
    id: string;
    threadId: string;
    payload: MessagePayload;
}

export interface MessagePayload {
    mimeType: string;
    headers: MessageHeader[];
}

export interface MessageHeader {
    name: HeaderType;
    value: string;
}

export enum HeaderType {
    From = "From",
    Subject = "Subject",
}

export const getMessage = (userId: string, messageId: string) => {
    return ApiRequest.get(
        `${baseUri}/users/${userId}/messages/${messageId}?format=metadata`,
    ).handler<Message>(jsonHandler);
};
