import * as React from "react";

import { Redirect, Route, RouteProps } from "react-router";

export interface ProtectedRouteData extends RouteProps {
    isAuthenticated: boolean;
    currentPath: string;
}

export interface ProtectedRouteHandlers {}

export default class ProtectedRoute extends React.PureComponent<
    ProtectedRouteData & ProtectedRouteHandlers
> {
    public render() {
        const { isAuthenticated, currentPath, ...otherProps } = this.props;

        if (!isAuthenticated) {
            return <Redirect to={`/login?redirectPath=${currentPath}`} />;
        }

        return <Route {...otherProps} />;
    }
}
