import { call, put, select, takeLatest } from "redux-saga/effects";

import { RootState } from "reducers";
import { loadMessages, loadMessagesFailure, loadMessagesSuccess } from "reducers/actions";

import * as gmailServices from "sagas/gmail";
import { Message, Profile } from "model";

export function* messagesSaga() {
    yield takeLatest(loadMessages.type, processLogin);
}

function* processLogin(_action: ReturnType<typeof loadMessages>) {
    try {
        const profile: Profile = yield call(getProfile);

        const messages: Message[] = yield call(gmailServices.loadMessages, profile.email);

        yield put(loadMessagesSuccess(messages));
    } catch (err) {
        yield put(loadMessagesFailure(err, err.toString()));
    }
}

function* getProfile() {
    const profile: RootState["profile"] = yield select((state: RootState) => state.profile);

    if (!profile.item) {
        throw new Error("User profile is not loaded");
    }

    return profile.item;
}
