import { connect } from "react-redux";

import { RootState } from "reducers";

import Settings, { SettingsData, SettingsHandlers } from "./Settings";
import { setGmailClientId } from "reducers/actions";

const mapStateToProps = (state: RootState): SettingsData => {
    return {
        clientId: state.settings.clientId || "",
    };
};

const mapDispatchToProps: SettingsHandlers = {
    onChangeClientId: setGmailClientId,
};

const SettingsModule = connect(
    mapStateToProps,
    mapDispatchToProps,
)(Settings);

export { SettingsModule as default, SettingsModule as Settings };
