import { connect } from "react-redux";

import { RootState } from "reducers";

import Messages, { MessagesData, MessagesHandlers } from "./Messages";

const mapStateToProps = (state: RootState): MessagesData => {
    return {
        messages: state.messages.items,
        isLoading: state.messages.isLoading,
    };
};

const mapDispatchToProps: MessagesHandlers = {};

const MessagesModule = connect(
    mapStateToProps,
    mapDispatchToProps,
)(Messages);

export { MessagesModule as default, MessagesModule as Messages };
