import { ApiRequest, jsonHandler } from "api/apiRequest";

const baseUri = "https://www.googleapis.com/gmail/v1";

export interface MessageList {
    messages: MessageInfo[];
    nextPageToken: string;
}

export interface MessageInfo {
    id: string;
    threadId: string;
}

export const getMessageList = (userId: string, maxResult: number = 20) => {
    return ApiRequest.get(`${baseUri}/users/${userId}/messages?maxResults=${maxResult}`).handler<
        MessageList
    >(jsonHandler);
};
