import { call } from "redux-saga/effects";

import { Message } from "model";

import * as gmailApi from "api/gmail";

import { sendAuthorizeRequestSaga } from "./sendAuthorizeRequestSaga";

export function* loadMessages(userId: string) {
    const messageList: gmailApi.MessageList = yield call(
        sendAuthorizeRequestSaga,
        gmailApi.getMessageList(userId, 10),
    );

    const messages: Message[] = [];

    for (const messageInfo of messageList.messages) {
        const message: gmailApi.Message = yield call(
            sendAuthorizeRequestSaga,
            gmailApi.getMessage(userId, messageInfo.id),
        );

        messages.push(parseMessage(message));
    }

    return messages;
}

const parseMessage = (message: gmailApi.Message): Message => {
    const from = message.payload.headers.find(x => x.name === gmailApi.HeaderType.From).value;
    const subject = message.payload.headers.find(x => x.name === gmailApi.HeaderType.Subject).value;

    return { id: message.id, from, subject };
};
