import { actionCreator } from "./action-helpers";

import { Profile } from "model/profile";

export const requestProfile = actionCreator("profile/PROFILE_REQUEST");

export const requestProfileSuccess = actionCreator("profile/PROFILE_SUCCESS", (profile: Profile) => ({
    profile,
}));

export const requestProfileFailure = actionCreator(
    "profile/PROFILE_FAILURE",
    (erorr: Error, message: string) => ({ erorr, message }),
);
