import { call, put, takeLatest } from "redux-saga/effects";

import {
    loadMessages,
    requestProfile,
    requestProfileFailure,
    requestProfileSuccess,
} from "reducers/actions";

import { loadProfile } from "sagas/gmail/loadProfile";

export function* profileSaga() {
    yield takeLatest(requestProfile.type, processLogin);
}

function* processLogin(_action: ReturnType<typeof requestProfile>) {
    try {
        const profile = yield call(loadProfile);

        yield put(requestProfileSuccess(profile));

        // TODO: Не должно тут быть
        yield put(loadMessages());
    } catch (err) {
        yield put(requestProfileFailure(err, err.toString()));
    }
}
