import * as React from "react";

import Tabs from "@material-ui/core/Tabs";

import { TabLink } from "modules/link";
import { Localize } from "modules/localize";

export interface NavigationData {
    path: string;
}

export interface NavigationHandlers {}

const PathNameMap = {
    "/": "navigation:home",
    "/settings": "navigation:settings",
    "/messages": "navigation:messages",
    "/video-react": "navigation:video",
};

export class Navigation extends React.PureComponent<NavigationData & NavigationHandlers> {
    public render() {
        const { path } = this.props;

        const isValidPath = Boolean(PathNameMap[path]);

        return (
            <Tabs value={isValidPath ? path : false}>
                {Object.keys(PathNameMap).map(path => (
                    <TabLink
                        key={path}
                        value={path}
                        to={path}
                        label={<Localize id={PathNameMap[path]} />}
                    />
                ))}
            </Tabs>
        );
    }
}

export default Navigation;
