import * as React from "react";
import { hot } from "react-hot-loader";

import { Route, Switch } from "react-router";

import { AppBar, CssBaseline } from "@material-ui/core";

import { Navigation } from "modules/navigation";
import { ProtectedRoute } from "modules/routes/protected-route";

import { Home } from "modules/pages/home";
import { Login } from "modules/pages/login";
import { Messages } from "modules/pages/messages";
import { Settings } from "modules/pages/settings";
import { VideoReactExample } from "modules/pages/video-react-example";

import * as styles from "./App.css";

const App = () => (
    <div className={styles.app}>
        <CssBaseline />
        <div className={styles["app__header"]}>
            <AppBar position="static">
                <Navigation />
            </AppBar>
        </div>
        <div className={styles["app__page"]}>
            <Switch>
                <Route exact path="/" component={Home} />
                <Route path="/login" component={Login} />
                <Route path="/video-react" component={VideoReactExample} />
                <Route path="/settings" component={Settings} />
                <ProtectedRoute path="/messages" component={Messages} />
            </Switch>
        </div>
    </div>
);

export default hot(module)(App);
