import { Reducer } from "redux";
import { Actions } from "reducers";

import { Message } from "model";

export interface State {
    isLoading: boolean;
    items?: Message[];
    errorMessage?: string;
}

const DefaulState: State = {
    isLoading: false,
};

const reducer: Reducer<State> = (state = DefaulState, action: Actions): State => {
    switch (action.type) {
        case "messages/LOAD": {
            return { isLoading: true };
        }
        case "messages/LOAD_SUCCESS": {
            const { messages } = action;
            return { isLoading: false, items: messages };
        }
        case "messages/LOAD_FAILURE": {
            const { message } = action;
            return { isLoading: false, errorMessage: message };
        }
        default: {
            return state;
        }
    }
};

export default reducer;
