import * as React from "react";

import Grid from "@material-ui/core/Grid";

import { Localize } from "modules/localize";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";

export interface SettingsData {
    clientId: string;
}

export interface SettingsHandlers {
    onChangeClientId: (clientId: string) => void;
}

export default class Settings extends React.PureComponent<SettingsData & SettingsHandlers> {
    public render() {
        const { clientId } = this.props;
        return (
            <Grid container justify="center" alignItems="center" direction="column">
                <Grid item>
                    <Typography variant="h6" color="inherit">
                        <Localize id="settings:enterCientId" />
                    </Typography>
                </Grid>
                <Grid item>
                    <TextField
                        id="outlined-name"
                        label={<Localize id="settings:clientId" />}
                        value={clientId}
                        onChange={this.handleChange}
                        margin="normal"
                        variant="outlined"
                    />
                </Grid>
            </Grid>
        );
    }

    private handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const clientId = event.currentTarget.value;

        const { onChangeClientId } = this.props;
        onChangeClientId(clientId);
    };
}
