import * as React from "react";

const classNames = require("classnames/bind");

import Fab from "@material-ui/core/Button";

import AccessAlarmIcon from "@material-ui/icons/PlayCircleOutline";
import FullscreenIcon from "@material-ui/icons/Fullscreen";

import Slider from "@material-ui/lab/Slider";

import styles from "./VideoPlayer.scss";
const style = classNames.bind(styles);

export interface VideoPlayerData {
    className?: string;
    videoFileURL?: string;
    subtitlesFileURL?: string;
}

export interface VideoPlayerHandlers {}

export interface VideoPlayerState {
    isFullscreen: boolean;
    isPaused: boolean;
    seekPosition: number;

    subtitles: string[];
}

export class VideoPlayer extends React.PureComponent<VideoPlayerData & VideoPlayerHandlers> {
    public state: VideoPlayerState = {
        isFullscreen: false,
        isPaused: true,
        seekPosition: 0,
        subtitles: [],
    };

    private videoPlayerElement: HTMLDivElement;
    private videoElement: HTMLVideoElement;
    private trackElement: HTMLTrackElement;

    public componentDidMount() {
        this.subscribe();
    }

    public render() {
        const { className, videoFileURL, subtitlesFileURL } = this.props;
        const { seekPosition, subtitles } = this.state;

        return (
            <div
                ref={component => {
                    this.videoPlayerElement = component;
                }}
                className={style(styles["video-player"], className)}
                onWheel={this.handleWheel}
            >
                <video
                    src={videoFileURL}
                    ref={component => {
                        this.videoElement = component;
                    }}
                    className={style(styles["video-player__video"])}
                    onClick={this.handlePlay}
                    onDoubleClick={this.toogleFullScreen}
                    onTimeUpdate={this.handleTimeUpdate}
                >
                    <track
                        ref={component => {
                            this.trackElement = component;
                        }}
                        style={{ display: "none" }}
                        kind="subtitles"
                        src={subtitlesFileURL}
                        default
                    />
                </video>
                {subtitles.length > 0 && (
                    <div className={style("video-player__subtitle")}>
                        {subtitles.map((subtitle, index) => (
                            <div key={index}>{subtitle}</div>
                        ))}
                    </div>
                )}
                <div className={style(styles["video-player__controls"])}>
                    <Fab color="primary" aria-label="Play" onClick={this.handlePlay}>
                        <AccessAlarmIcon />
                    </Fab>
                    <Slider
                        className={style(styles["video-player__slider"])}
                        value={seekPosition}
                        onChange={this.handleChangeSeekPosition}
                    />
                    <Fab color="primary" aria-label="Fullscreen" onClick={this.toogleFullScreen}>
                        <FullscreenIcon />
                    </Fab>
                </div>
            </div>
        );
    }

    private subscribe = () => {
        // TODO: Подписки нужно делать лишь раз
        if (this.trackElement) {
            const textTrack = this.trackElement.track;

            textTrack.mode = "hidden";
            textTrack.oncuechange = this.handleCueChange;
        }
    };

    private handleChangeSeekPosition = (_event: React.ChangeEvent<{}>, value: number) => {
        if (this.videoElement) {
            const duration = this.videoElement.duration;
            const currentTime = duration * (value / 100);

            this.videoElement.currentTime = currentTime;
        }
    };

    private handlePlay = () => {
        if (this.videoElement) {
            const { isPaused } = this.state;

            if (isPaused) {
                this.videoElement.play();
            } else {
                this.videoElement.pause();
            }

            this.setState({ isPaused: !isPaused });
        }
    };

    private handleWheel = (event: React.WheelEvent<HTMLDivElement>) => {
        if (this.videoElement) {
            if (event.deltaY > 0) {
                this.videoElement.currentTime -= 5; // to const
            } else {
                this.videoElement.currentTime += 5;
            }
        }

        // event.preventDefault();
    };

    private handleTimeUpdate = () => {
        if (this.videoElement) {
            const duration = this.videoElement.duration;
            const currentTime = this.videoElement.currentTime;

            const seekPosition = (100 / duration) * currentTime;

            this.setState({ seekPosition });
        }
    };

    private handleCueChange = () => {
        if (this.trackElement) {
            const textTrack = this.trackElement.track;

            const subtitles = [];

            for (const cue of textTrack.activeCues) {
                const text = cue.text.replace(/(<([^>]+)>)/gi, "");
                subtitles.push(text);
            }

            this.setState({ subtitles });
        }
    };

    private toogleFullScreen = () => {
        const { isFullscreen } = this.state;

        if (isFullscreen) {
            document.exitFullscreen().catch(errorLogger);
        } else {
            this.videoPlayerElement.requestFullscreen().catch(errorLogger);
        }

        this.setState({ isFullscreen: !isFullscreen });
    };
}

const errorLogger = reason => {
    // tslint:disable-next-line:no-console
    console.log("reason", JSON.stringify(reason, null, " "));
};
