import { call } from "redux-saga/effects";

import { Profile } from "model/profile";

import * as gmailApi from "api/gmail";

import { sendAuthorizeRequestSaga } from "./sendAuthorizeRequestSaga";

export function* loadProfile() {
    const profile: gmailApi.Profile = yield call(
        sendAuthorizeRequestSaga,
        gmailApi.getProfile("me"),
    );
    return parseProfile(profile);
}

const parseProfile = (profile: gmailApi.Profile): Profile => {
    return { email: profile.emailAddress };
};
