import * as React from "react";

const classNames = require("classnames/bind");

import CircularProgress from "@material-ui/core/CircularProgress";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";

import Localize from "modules/localize";

import styles from "./Messages.scss";
const style = classNames.bind(styles);

import { Message } from "./types";

export interface MessagesData {
    isLoading: boolean;
    messages?: Message[];
}

export interface MessagesHandlers {}

export default class Messages extends React.PureComponent<MessagesData & MessagesHandlers> {
    public render() {
        const { isLoading } = this.props;

        return (
            <Grid container justify="center" alignItems="center" className={style(styles.messages)}>
                {isLoading ? this.renderPreloader() : this.renderContent()}
            </Grid>
        );
    }

    private renderPreloader = () => {
        return <CircularProgress />;
    };

    private renderContent = () => {
        const { messages } = this.props;

        if (!messages) {
            return null;
        }

        return (
            <Paper>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>
                                <Localize id="messages:from" />
                            </TableCell>
                            <TableCell>
                                <Localize id="messages:subject" />
                            </TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {messages.map(row => (
                            <TableRow key={row.id}>
                                <TableCell>{row.from}</TableCell>
                                <TableCell>{row.subject}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </Paper>
        );
    };
}
